.sidebar a {
  text-decoration: none;
  color: rgba(0, 0, 0, 0.493);
  font: 200 1rem "Roboto Slab";
  padding: 2rem;
  transition: all 0.5s;
}
.sidebar a:hover {
  color: #800039;
  transform: scale(1.1);
}

.sidebar a > P {
  display: flex;
  align-items: center;
}
.sidebar a > p > svg {
  margin-right: 0.5rem;
  color: #800039;
}

.MuiTypography-root {
  background-color: #fff !important;
